
'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Video, VideoOff, AlertCircle, Loader2, Copy, LogIn, Users, User, UserMinus, UserPlus, PhoneOff, Mic, MicOff } from 'lucide-react'; // Added User icon
import { useTranslation } from 'react-i18next';
import Link from 'next/link';
import { io, type Socket } from 'socket.io-client'; // Import socket.io-client
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

// Simple unique code generation (replace with a more robust solution if needed)
function generateMeetingCode(): string {
  return Math.random().toString(36).substring(2, 8).toUpperCase();
}

// Type for participant data received from socket
interface Participant {
    id: string; // Socket ID
    name: string; // User name
}

// Placeholder for WebRTC configuration
const peerConnectionConfig: RTCConfiguration = {
  iceServers: [
    { urls: 'stun:stun.l.google.com:19302' }, // Example STUN server
    // Add TURN servers here for production environments if needed
  ],
};

export default function VideoCallPage() {
  const router = useRouter();
  const { user, loading: authLoading } = useAuth();
  const { toast } = useToast();
  const { t } = useTranslation();
  const localVideoRef = useRef<HTMLVideoElement>(null);
  const remoteVideosRef = useRef<HTMLDivElement>(null); // Ref for remote video container
  const socketRef = useRef<Socket | null>(null);
  const peerConnectionsRef = useRef<Record<string, RTCPeerConnection>>({}); // Store peer connections { socketId: RTCPeerConnection }
  const localStreamRef = useRef<MediaStream | null>(null);

  const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null); // null initially, true/false after check
  const [isMeetingActive, setIsMeetingActive] = useState(false);
  const [meetingCode, setMeetingCode] = useState(''); // Room ID
  const [isLoading, setIsLoading] = useState(true);
  const [isConnecting, setIsConnecting] = useState(false); // Loading state for joining/starting
  const [participantList, setParticipantList] = useState<Participant[]>([]); // Store participant info {id, name}
  const [isAudioMuted, setIsAudioMuted] = useState(false);
  const [isVideoOff, setIsVideoOff] = useState(false);
  const [connectionError, setConnectionError] = useState<string | null>(null); // State for connection errors


  // --- Socket.IO Connection ---
  useEffect(() => {
    if (!user || authLoading) return; // Don't connect if no user or still loading auth

    setConnectionError(null); // Reset error on attempt

    const socketServerUrl = process.env.NEXT_PUBLIC_SOCKET_SERVER_URL;

    // Check if the URL is configured
    if (!socketServerUrl) {
      const errorMsg = t('page.videoCall.error.configMissing');
      console.error(errorMsg);
      toast({ variant: 'destructive', title: t('error.genericTitle'), description: errorMsg });
      setConnectionError(errorMsg);
      return; // Stop execution if URL is missing
    }

    // Log the actual URL being used
    console.log("[VideoCallPage] Attempting to connect to Socket.IO server at:", socketServerUrl);

    // Prevent multiple connections
    if (socketRef.current) {
        console.log("[VideoCallPage] Socket already exists, skipping new connection.");
        return;
    }


    // Try forcing WebSocket transport first, as polling might be causing issues
    const socket = io(socketServerUrl, {
        transports: ['websocket', 'polling'], // Prioritize WebSocket
        reconnectionAttempts: 3, // Limit reconnection attempts
        // Consider adding timeout if connections hang indefinitely
        // timeout: 5000, // e.g., 5 seconds
    });
    socketRef.current = socket;

    socket.on('connect', () => {
      console.log('[VideoCallPage] Connected to Socket.IO server with ID:', socket.id);
      toast({ title: t('page.videoCall.toast.connectedTitle'), description: t('page.videoCall.toast.connectedDescription') });
      setConnectionError(null); // Clear error on successful connection
    });

    socket.on('connect_error', (error) => {
      // The error object might be complex, log relevant parts
      const errorMessage = error.message || 'Unknown connection error';
      const causeMessage = error.cause?.message || 'No cause provided'; // Get cause, if available
      console.error('[VideoCallPage] Socket connection error:', errorMessage, 'Cause:', causeMessage, error); // Log full error object
      const errorMsg = t('page.videoCall.error.connectionFailed', { url: socketServerUrl, details: errorMessage + (error.cause ? `. Cause: ${causeMessage}` : '') });
      toast({ variant: 'destructive', title: t('page.videoCall.error.connectionFailedTitle'), description: errorMsg, duration: 10000 });
      setConnectionError(errorMsg); // Set error state
      // Clean up if connection fails
      socket.disconnect();
      socketRef.current = null;
      setIsMeetingActive(false); // Ensure meeting state is reset
      setIsConnecting(false);
    });

    socket.on('disconnect', (reason) => {
      console.log('[VideoCallPage] Disconnected from Socket.IO server:', reason);
      // Only show error and reset if actively in a meeting when disconnected
      if (isMeetingActive) {
        handleLeaveMeeting(false); // Clean up state without emitting 'leave'
        const errorMsg = t('page.videoCall.error.disconnected');
        toast({ variant: 'destructive', title: t('page.videoCall.error.disconnectedTitle'), description: errorMsg });
        setConnectionError(errorMsg); // Set error state
      }
      // Ensure socketRef is nulled out on disconnect
      socketRef.current = null;
    });

    // --- Room and Participant Handling ---
    socket.on('room-created', (roomId: string) => {
        console.log(`[VideoCallPage] Room created: ${roomId}`);
        setIsMeetingActive(true);
        setIsConnecting(false);
        setMeetingCode(roomId);
        // Add self immediately upon creation acknowledgment
        if(user && socket.id) {
            setParticipantList([{ id: socket.id, name: user.name }]);
        }
        toast({ title: t('page.videoCall.toast.startedTitle'), description: t('page.videoCall.toast.startedDescription', { code: roomId }) });
        setConnectionError(null); // Clear error state
    });

    socket.on('room-joined', (roomId: string, participants: Participant[]) => {
        console.log(`[VideoCallPage] Successfully joined room: ${roomId}`);
        setIsMeetingActive(true);
        setIsConnecting(false);
        setMeetingCode(roomId); // Ensure meeting code is set
        setParticipantList(participants); // Set full list from server
        toast({ title: t('page.videoCall.toast.joiningTitle'), description: t('page.videoCall.toast.joiningDescription', { code: roomId }) });
        // When joining, signal readiness to existing participants to initiate peer connections
        if (socketRef.current) {
            socketRef.current.emit('ready', roomId);
        }
        setConnectionError(null); // Clear error state
    });

    socket.on('room-join-error', (errorMsg: string) => {
        console.error('[VideoCallPage] Error joining room:', errorMsg);
        setIsConnecting(false);
        toast({ variant: 'destructive', title: t('page.videoCall.toast.joinFailedTitle'), description: errorMsg });
        setConnectionError(errorMsg); // Set error state
    });

    socket.on('update-participants', (participants: Participant[]) => {
        console.log('[VideoCallPage] Participant list updated:', participants);
        // Compare new list with old to show join/leave toasts
        setParticipantList((prevList) => {
            const prevIds = new Set(prevList.map(p => p.id));
            const currentIds = new Set(participants.map(p => p.id));

            participants.forEach(p => {
                // Only show toast if the user wasn't already in the list AND it's not the current user
                if (!prevIds.has(p.id) && p.id !== socketRef.current?.id) {
                    toast({ title: t('page.videoCall.toast.participantJoined', { name: p.name }), variant: 'default' });
                }
            });

            prevList.forEach(p => {
                 // Only show toast if the user is no longer in the list AND it's not the current user
                if (!currentIds.has(p.id) && p.id !== socketRef.current?.id) {
                    toast({ title: t('page.videoCall.toast.participantLeft', { name: p.name }), variant: 'default' });
                     // Clean up peer connection for the user who left
                    if (peerConnectionsRef.current[p.id]) {
                         peerConnectionsRef.current[p.id].close();
                         delete peerConnectionsRef.current[p.id];
                         removeRemoteVideo(p.id);
                         console.log(`[VideoCallPage] Closed peer connection and removed video for user ${p.id}`);
                     }
                }
            });
            return participants; // Update state with the new list from server
        });
    });

    // --- WebRTC Signaling Handling ---
    socket.on('user-ready', (userId: string, userName: string) => {
        // Check if it's not self-ready signal
        if(userId !== socketRef.current?.id) {
            console.log(`[VideoCallPage] User ${userName} (${userId}) is ready. Initiating peer connection.`);
            initiatePeerConnection(userId, true); // Initiate connection as the caller
        }
    });

    socket.on('offer', async (senderId: string, offer: RTCSessionDescriptionInit) => {
        if (senderId === socketRef.current?.id) return; // Ignore offers from self
        console.log(`[VideoCallPage] Received offer from ${senderId}`);
        const pc = initiatePeerConnection(senderId, false); // Initiate connection as the callee
        try {
            await pc.setRemoteDescription(new RTCSessionDescription(offer));
            const answer = await pc.createAnswer();
            await pc.setLocalDescription(answer);
            if(socketRef.current) {
                socketRef.current.emit('answer', meetingCode, senderId, answer);
                console.log(`[VideoCallPage] Sent answer to ${senderId}`);
            }
        } catch (error) {
            console.error('[VideoCallPage] Error handling offer:', error);
        }
    });

     socket.on('answer', async (senderId: string, answer: RTCSessionDescriptionInit) => {
        if (senderId === socketRef.current?.id) return; // Ignore answers from self
        console.log(`[VideoCallPage] Received answer from ${senderId}`);
        const pc = peerConnectionsRef.current[senderId];
        if (pc && pc.signalingState === 'have-local-offer') {
            try {
                await pc.setRemoteDescription(new RTCSessionDescription(answer));
                console.log(`[VideoCallPage] Connection established with ${senderId}`);
            } catch (error) {
                console.error('[VideoCallPage] Error handling answer:', error);
            }
        } else {
             console.warn(`[VideoCallPage] Received answer from ${senderId}, but no local offer or connection doesn't exist/unexpected state: ${pc?.signalingState}`);
        }
    });


    socket.on('ice-candidate', async (senderId: string, candidate: RTCIceCandidateInit | null) => {
        if (senderId === socketRef.current?.id) return; // Ignore own candidates
        if (!candidate) {
            console.log(`[VideoCallPage] Received null ICE candidate from ${senderId}, signaling end.`);
            return;
        }
        console.log(`[VideoCallPage] Received ICE candidate from ${senderId}`);
        const pc = peerConnectionsRef.current[senderId];
        if (pc && pc.connectionState !== 'closed') { // Only add candidate if connection exists and isn't closed
            try {
                // Queue candidates if remote description isn't set yet
                if(pc.remoteDescription) {
                    await pc.addIceCandidate(new RTCIceCandidate(candidate));
                } else {
                    console.log(`[VideoCallPage] Queuing ICE candidate from ${senderId} as remote description is not set yet.`);
                    // Simple queue (more robust queueing might be needed for complex scenarios)
                    const queue = (pc as any).candidateQueue || [];
                    queue.push(candidate);
                    (pc as any).candidateQueue = queue;
                }
            } catch (error) {
                // Ignore benign errors like adding candidate before remote description is fully set
                if (error instanceof DOMException && error.name === 'InvalidStateError') {
                    console.warn(`[VideoCallPage] Ignored InvalidStateError when adding ICE candidate for ${senderId}. Probably added before SRD.`);
                } else {
                    console.error('[VideoCallPage] Error adding received ICE candidate:', error);
                }
            }
        } else {
            console.warn(`[VideoCallPage] Received ICE candidate from ${senderId}, but peer connection doesn't exist or is closed.`);
        }
    });


    // Cleanup on component unmount
    return () => {
      console.log("[VideoCallPage] Cleaning up video call component...");
      // Ensure handleLeaveMeeting logic runs fully
      handleLeaveMeeting(false); // Pass false to prevent emitting 'leave' on unmount

      // Explicitly disconnect socket if it exists
      if (socketRef.current) {
          console.log("[VideoCallPage] Disconnecting socket on unmount.");
          socketRef.current.disconnect();
          socketRef.current = null;
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user, authLoading]); // Re-run if user or authLoading changes


  // --- Camera Permission ---
  const requestCameraPermission = useCallback(async (onSuccess?: () => void, onFailure?: () => void) => {
    console.log("[VideoCallPage] Requesting camera/mic permission...");
    setIsLoading(true);
    try {
        // Stop existing stream first, if any
        if (localStreamRef.current) {
            localStreamRef.current.getTracks().forEach(track => track.stop());
            localStreamRef.current = null;
        }
        const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
        localStreamRef.current = stream; // Store the new stream
        setHasCameraPermission(true);
        if (localVideoRef.current) {
            localVideoRef.current.srcObject = stream;
            localVideoRef.current.muted = true; // Mute local video playback
        }
        console.log("[VideoCallPage] Camera/Mic access granted.");
        setIsLoading(false); // Moved inside success path
        onSuccess?.(); // Call success callback
    } catch (error) {
        console.error('[VideoCallPage] Error accessing camera/microphone:', error);
        setHasCameraPermission(false);
        localStreamRef.current = null; // Ensure stream is null on error
        toast({
            variant: 'destructive',
            title: t('page.videoCall.error.permissionDeniedTitle'),
            description: t('page.videoCall.error.permissionDenied'),
        });
        setIsLoading(false); // Moved inside failure path
        onFailure?.();
    }
    // Removed finally block as isLoading is set within try/catch
  }, [toast, t]);

  // --- WebRTC Peer Connection Logic ---
  const initiatePeerConnection = (remoteSocketId: string, isInitiator: boolean): RTCPeerConnection => {
    if (peerConnectionsRef.current[remoteSocketId]) {
         console.log(`[VideoCallPage] Peer connection already exists for ${remoteSocketId}. Reusing.`);
         // Potentially check state and reset if necessary, but reusing is often okay
         return peerConnectionsRef.current[remoteSocketId];
     }

    console.log(`[VideoCallPage] Initiating peer connection with ${remoteSocketId}. Initiator: ${isInitiator}`);
    const pc = new RTCPeerConnection(peerConnectionConfig);
    peerConnectionsRef.current[remoteSocketId] = pc;

    // Add local stream tracks
    if (localStreamRef.current) {
      localStreamRef.current.getTracks().forEach(track => {
        try {
            pc.addTrack(track, localStreamRef.current!);
            console.log(`[VideoCallPage] Added local ${track.kind} track to connection with ${remoteSocketId}`);
        } catch (error) {
             console.error(`[VideoCallPage] Error adding local ${track.kind} track:`, error);
        }
      });
    } else {
        console.warn("[VideoCallPage] Local stream not available when initiating peer connection.");
        // Consider requesting permission here or handling the lack of stream gracefully
    }

    // Handle incoming remote tracks
    pc.ontrack = (event) => {
        console.log(`[VideoCallPage] Received remote track from ${remoteSocketId}:`, event.track.kind);
        if (event.streams && event.streams[0]) {
            addRemoteVideo(remoteSocketId, event.streams[0]);
        } else {
            // Handle cases where track might not have a stream initially (less common)
            const inboundStream = new MediaStream([event.track]);
            addRemoteVideo(remoteSocketId, inboundStream);
        }
    };

    // Handle ICE candidates
    pc.onicecandidate = (event) => {
      if (event.candidate && socketRef.current && meetingCode) {
        // console.log(`[VideoCallPage] Sending ICE candidate to ${remoteSocketId}`);
        socketRef.current.emit('ice-candidate', meetingCode, remoteSocketId, event.candidate);
      } else if (!event.candidate) {
         console.log(`[VideoCallPage] Finished gathering ICE candidates for ${remoteSocketId}.`);
         // Optionally send a null candidate to signal completion, depending on backend implementation
         // socketRef.current?.emit('ice-candidate', meetingCode, remoteSocketId, null);
      }
    };

     pc.onicecandidateerror = (event) => {
         console.error(`[VideoCallPage] ICE candidate error for ${remoteSocketId}:`, event);
     };

    // Handle connection state changes
    pc.onconnectionstatechange = () => {
      const state = pc.connectionState;
      console.log(`[VideoCallPage] Connection state with ${remoteSocketId} changed to: ${state}`);
      if (state === 'failed' || state === 'disconnected' || state === 'closed') {
          console.warn(`[VideoCallPage] Connection with ${remoteSocketId} ${state}. Cleaning up.`);
          // Clean up connection and UI
          if (peerConnectionsRef.current[remoteSocketId]) {
             peerConnectionsRef.current[remoteSocketId].close();
             delete peerConnectionsRef.current[remoteSocketId];
          }
          removeRemoteVideo(remoteSocketId);
          // Optionally trigger participant list update if server doesn't handle disconnects fast enough
          // Maybe emit a signal or rely on server's participant update
      }
    };

    pc.onsignalingstatechange = async () => {
        console.log(`[VideoCallPage] Signaling state with ${remoteSocketId} changed to: ${pc.signalingState}`);
        // Process queued candidates if SRD is set and state allows
        if (pc.remoteDescription && pc.signalingState === 'stable') {
             const queue = (pc as any).candidateQueue || [];
             console.log(`[VideoCallPage] Processing ${queue.length} queued candidates for ${remoteSocketId}`);
             while (queue.length) {
                 const candidate = queue.shift();
                 try {
                     await pc.addIceCandidate(new RTCIceCandidate(candidate));
                 } catch (error) {
                     console.error(`[VideoCallPage] Error adding queued ICE candidate for ${remoteSocketId}:`, error);
                 }
             }
             (pc as any).candidateQueue = [];
        }
    };


    // If initiator, create offer
    if (isInitiator) {
        // Ensure local stream tracks are added before creating offer
        if (!localStreamRef.current) {
            console.warn("[VideoCallPage] Attempting to create offer without a local stream.");
            // Optionally wait for stream or handle error
        }
        pc.createOffer()
        .then(offer => pc.setLocalDescription(offer))
        .then(() => {
          if (socketRef.current && meetingCode) {
              console.log(`[VideoCallPage] Sending offer to ${remoteSocketId}`);
              socketRef.current.emit('offer', meetingCode, remoteSocketId, pc.localDescription);
          }
        })
        .catch(error => console.error('[VideoCallPage] Error creating offer:', error));
    }

    return pc;
  };

  // --- Remote Video Handling ---
  const addRemoteVideo = (socketId: string, stream: MediaStream) => {
    if (!remoteVideosRef.current) return;

    let videoWrapper = remoteVideosRef.current.querySelector(`div[data-socket-id="${socketId}"]`);
    let videoElement: HTMLVideoElement | null = null;
    let nameElement: HTMLDivElement | null = null;
    // Find participant name - use participantList which is updated by socket events
    const participant = participantList.find(p => p.id === socketId);

    if (!videoWrapper) {
        console.log(`[VideoCallPage] Creating video wrapper for remote user ${socketId}`);
        videoWrapper = document.createElement('div');
        videoWrapper.setAttribute('data-socket-id', socketId);
        videoWrapper.className = 'aspect-video bg-muted rounded-lg overflow-hidden relative border'; // Style the wrapper

        videoElement = document.createElement('video');
        videoElement.autoplay = true;
        videoElement.playsInline = true;
        videoElement.className = 'w-full h-full object-cover';
        videoWrapper.appendChild(videoElement);

        nameElement = document.createElement('div');
        nameElement.className = 'absolute bottom-2 left-2 bg-black/50 text-white text-xs px-2 py-1 rounded z-10'; // Ensure name is on top
        nameElement.textContent = participant ? participant.name : socketId.substring(0, 6); // Display name or short ID
        videoWrapper.appendChild(nameElement);

        remoteVideosRef.current.appendChild(videoWrapper);
    } else {
        console.log(`[VideoCallPage] Updating stream for existing video element for user ${socketId}`);
        videoElement = videoWrapper.querySelector('video');
        nameElement = videoWrapper.querySelector('.absolute.bottom-2.left-2');
        if(nameElement && participant && nameElement.textContent !== participant.name) {
             nameElement.textContent = participant.name; // Update name if changed
        }
    }

    // Assign stream if video element exists and stream is different
    if (videoElement && videoElement.srcObject !== stream) {
        videoElement.srcObject = stream;
        // Attempt to play, might be needed in some browsers
        videoElement.play().catch(e => console.warn(`[VideoCallPage] Video play failed for ${socketId}:`, e));
    }
  };


  const removeRemoteVideo = (socketId: string) => {
    if (!remoteVideosRef.current) return;
    const videoWrapper = remoteVideosRef.current.querySelector(`div[data-socket-id="${socketId}"]`);
    if (videoWrapper) {
      console.log(`[VideoCallPage] Removing video element for remote user ${socketId}`);
      const videoElement = videoWrapper.querySelector('video');
      // Stop tracks before removing element
      if (videoElement && videoElement.srcObject) {
          (videoElement.srcObject as MediaStream).getTracks().forEach(track => track.stop());
          videoElement.srcObject = null; // Clear srcObject
      }
      // Use parentNode?.removeChild for safer removal
      videoWrapper.parentNode?.removeChild(videoWrapper);
    }
  };


  // --- Meeting Actions ---
  const handleJoinMeeting = async () => {
    if (!meetingCode.trim() || !user || !meetingCode.match(/^[A-Z0-9]{6}$/)) {
         toast({ variant: 'destructive', title: t('page.videoCall.error.invalidCodeTitle'), description: t('page.videoCall.error.invalidCodeDescription') });
         return;
    }

    if (!socketRef.current || !socketRef.current.connected) {
         const errorMsg = t('page.videoCall.error.notConnected');
         console.error(errorMsg);
         toast({ variant: 'destructive', title: t('page.videoCall.error.connectionFailedTitle'), description: errorMsg });
         setConnectionError(errorMsg);
         return;
     }

    setIsConnecting(true); // Set loading state
    setConnectionError(null); // Clear previous errors

    const joinAction = () => {
        if (socketRef.current) {
            console.log(`[VideoCallPage] Attempting to join room: ${meetingCode} as user ${user.name}`);
            socketRef.current.emit('join-room', meetingCode, user.name);
            // Socket listeners will handle 'room-joined' or 'room-join-error'
        } else {
             console.error("[VideoCallPage] Socket not connected, cannot join room.");
             setIsConnecting(false);
             const errorMsg = t('page.videoCall.error.notConnected');
             toast({ variant: 'destructive', title: t('page.videoCall.error.connectionFailedTitle'), description: errorMsg });
             setConnectionError(errorMsg);
        }
    };

    const failureAction = () => {
        setIsConnecting(false); // Reset connecting state if permission fails
    };

    if (hasCameraPermission === true) {
      joinAction();
    } else if (hasCameraPermission === false) {
      toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
      setIsConnecting(false);
    } else {
      // Request permission, join on success, handle failure
      await requestCameraPermission(joinAction, failureAction);
    }
  };

  const handleGenerateCode = async () => {
      if (!user) return;

       if (!socketRef.current || !socketRef.current.connected) {
         const errorMsg = t('page.videoCall.error.notConnected');
         console.error(errorMsg);
         toast({ variant: 'destructive', title: t('page.videoCall.error.connectionFailedTitle'), description: errorMsg });
         setConnectionError(errorMsg);
         return;
       }

      setIsConnecting(true);
      setConnectionError(null);
      const newCode = generateMeetingCode(); // Generate code locally first

      const startAction = () => {
          if (socketRef.current) {
              console.log(`[VideoCallPage] Attempting to create room: ${newCode} as user ${user.name}`);
              socketRef.current.emit('create-room', newCode, user.name);
              // Socket listener will handle 'room-created'
          } else {
              console.error("[VideoCallPage] Socket not connected, cannot create room.");
              setIsConnecting(false);
              const errorMsg = t('page.videoCall.error.notConnected');
              toast({ variant: 'destructive', title: t('page.videoCall.error.connectionFailedTitle'), description: errorMsg });
              setConnectionError(errorMsg);
          }
      };

      const failureAction = () => {
        setIsConnecting(false); // Reset connecting state if permission fails
      };


      if (hasCameraPermission === true) {
          startAction();
      } else if (hasCameraPermission === false) {
          toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
          setIsConnecting(false);
      } else {
          await requestCameraPermission(startAction, failureAction);
      }
  };


  const handleLeaveMeeting = (emitLeave = true) => {
    console.log("[VideoCallPage] Leaving meeting...");

    // Stop local media stream first
    if (localStreamRef.current) {
      console.log("[VideoCallPage] Stopping local media tracks.");
      localStreamRef.current.getTracks().forEach(track => track.stop());
      localStreamRef.current = null;
    }
    // Clear local video display
    if (localVideoRef.current) {
        localVideoRef.current.srcObject = null;
    }

    // Close all peer connections
    console.log("[VideoCallPage] Closing peer connections.");
    Object.entries(peerConnectionsRef.current).forEach(([id, pc]) => {
        console.log(`[VideoCallPage] Closing connection with ${id}`);
        pc.close();
        removeRemoteVideo(id); // Remove video element as well
    });
    peerConnectionsRef.current = {}; // Clear the refs

    // Clear remote videos container just in case
    if (remoteVideosRef.current) {
        remoteVideosRef.current.innerHTML = '';
    }

    // Emit leave event if requested and socket is connected
    if (emitLeave && socketRef.current && socketRef.current.connected && isMeetingActive && meetingCode) {
        console.log(`[VideoCallPage] Emitting leave-room for ${meetingCode}`);
        socketRef.current.emit('leave-room', meetingCode);
    }

    // Reset component state
    setIsMeetingActive(false);
    setMeetingCode('');
    setParticipantList([]); // Clear participant list
    setIsConnecting(false);
    setIsAudioMuted(false);
    setIsVideoOff(false);
    // Don't reset hasCameraPermission here

    if (emitLeave) { // Only show toast if user actively left
         toast({ title: t('page.videoCall.toast.leftMeeting') });
    }
     console.log("[VideoCallPage] Leave meeting cleanup complete.");
  };

  const toggleAudio = () => {
     if (localStreamRef.current) {
        const audioTracks = localStreamRef.current.getAudioTracks();
        if (audioTracks.length > 0) {
            const newState = !audioTracks[0].enabled;
            audioTracks[0].enabled = newState;
            setIsAudioMuted(!newState); // Muted state is inverse of enabled
            toast({ title: newState ? t('page.videoCall.toast.micOn') : t('page.videoCall.toast.micOff') });
            console.log(`[VideoCallPage] Microphone ${newState ? 'enabled' : 'disabled'}`);
        } else {
            console.warn("[VideoCallPage] No audio track found to toggle.");
        }
    } else {
        console.warn("[VideoCallPage] Local stream not available to toggle audio.");
    }
  };

  const toggleVideo = () => {
      if (localStreamRef.current) {
        const videoTracks = localStreamRef.current.getVideoTracks();
        if (videoTracks.length > 0) {
            const newState = !videoTracks[0].enabled;
            videoTracks[0].enabled = newState;
            setIsVideoOff(!newState); // Video off state is inverse of enabled
            toast({ title: newState ? t('page.videoCall.toast.cameraOn') : t('page.videoCall.toast.cameraOff') });
            console.log(`[VideoCallPage] Camera ${newState ? 'enabled' : 'disabled'}`);
        } else {
             console.warn("[VideoCallPage] No video track found to toggle.");
        }
    } else {
        console.warn("[VideoCallPage] Local stream not available to toggle video.");
    }
  };


  const copyCodeToClipboard = () => {
    if (meetingCode) {
      navigator.clipboard.writeText(meetingCode)
        .then(() => {
          toast({ title: t('page.videoCall.toast.codeCopied') });
        })
        .catch(err => {
          console.error('[VideoCallPage] Failed to copy meeting code: ', err);
          toast({ variant: 'destructive', title: t('page.videoCall.error.copyFailed') });
        });
    }
  };

  // --- Render Logic ---

  if (authLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  if (!user) {
    return (
      <div className="text-center py-10">
        <AlertCircle className="mx-auto h-12 w-12 text-destructive mb-4" />
        <h1 className="text-2xl font-semibold mb-2">{t('toast.accessDenied.title')}</h1>
        <p className="text-muted-foreground mb-6">{t('accessDenied.loginRequired.page', { pageName: t('header.videoCall') })}</p>
        <Link href="/login">
          <Button><LogIn className="mr-2 h-4 w-4" /> {t('login.loginButton')}</Button>
        </Link>
      </div>
    );
  }

  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl font-bold tracking-tight flex items-center">
            <Video className="mr-3 h-8 w-8 text-primary" />
            {t('page.videoCall.title')}
          </CardTitle>
          <CardDescription>
            {isMeetingActive
              ? t('page.videoCall.description.inMeeting', { code: meetingCode })
              : t('page.videoCall.description.joinOrCreate')}
          </CardDescription>
           <Alert variant="destructive" className="mt-4">
             <AlertCircle className="h-4 w-4" />
             <AlertTitle>{t('page.videoCall.warning.backendTitle')}</AlertTitle>
             <AlertDescription>
                {t('page.videoCall.warning.backendDescription', { url: process.env.NEXT_PUBLIC_SOCKET_SERVER_URL || t('page.videoCall.warning.urlNotSet') })}
             </AlertDescription>
          </Alert>
           {/* Display connection error prominently if it occurs before joining */}
           {connectionError && !isMeetingActive && (
             <Alert variant="destructive" className="mt-4">
               <AlertCircle className="h-4 w-4" />
               <AlertTitle>{t('page.videoCall.error.connectionFailedTitle')}</AlertTitle>
               <AlertDescription>
                 {connectionError}
                 <br />
                 {t('page.videoCall.error.checkServer')}
               </AlertDescription>
             </Alert>
            )}
        </CardHeader>
        <CardContent className="space-y-6">
          {isMeetingActive ? (
            // --- Meeting Active View ---
            <div className="space-y-4">
              {/* Video Grid */}
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 p-4 border rounded-lg bg-muted/20">
                  {/* Local Video */}
                  <div className="aspect-video bg-muted rounded-lg overflow-hidden relative border shadow-md">
                    <video ref={localVideoRef} className="w-full h-full object-cover" autoPlay muted playsInline />
                    <div className="absolute bottom-2 left-2 bg-black/60 text-white text-xs px-2 py-1 rounded z-10">
                        {user.name} ({t('page.videoCall.you')})
                    </div>
                    {/* Mute/Video Off Indicators for Local */}
                    <div className="absolute top-2 right-2 flex gap-1 z-10">
                         {isAudioMuted && <MicOff className="h-5 w-5 text-destructive bg-black/50 p-1 rounded-full" />}
                         {isVideoOff && <VideoOff className="h-5 w-5 text-destructive bg-black/50 p-1 rounded-full" />}
                    </div>

                    {/* Loading/Permission States Overlays */}
                    {hasCameraPermission === false && (
                        <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/70 text-destructive-foreground p-4 text-center z-20">
                            <VideoOff className="h-16 w-16 mb-4" />
                            <p className="text-lg font-semibold">{t('page.videoCall.error.permissionDeniedTitle')}</p>
                            <p>{t('page.videoCall.error.permissionInstructions')}</p>
                        </div>
                    )}
                    {hasCameraPermission === null && isLoading && (
                        <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/70 text-white p-4 z-20">
                            <Loader2 className="h-12 w-12 animate-spin mb-4" />
                            <p>{t('page.videoCall.requestingPermission')}</p>
                        </div>
                    )}
                     {isVideoOff && (
                          <div className="absolute inset-0 flex items-center justify-center bg-black/50 text-white z-10">
                              {/* Display user initials or icon when video is off */}
                              <span className="text-4xl font-semibold">{user.name.charAt(0).toUpperCase()}</span>
                              {/* <User className="h-16 w-16" /> */}
                          </div>
                      )}
                  </div>

                   {/* Remote Videos Container - elements added dynamically */}
                  <div ref={remoteVideosRef} className="col-span-1 sm:col-span-1 lg:col-span-2 grid grid-cols-1 md:grid-cols-2 gap-4">
                    {/* Placeholder shown only if truly no *other* participants */}
                    {participantList.filter(p => p.id !== socketRef.current?.id).length === 0 && (
                        <div className="aspect-video bg-muted/50 rounded-lg flex items-center justify-center text-muted-foreground text-center p-4 md:col-span-2">
                            {t('page.videoCall.waitingForOthers')}
                        </div>
                    )}
                     {/* Remote video wrappers will be appended here by addRemoteVideo */}
                  </div>
               </div>


              {/* Meeting Info & Controls Row */}
              <div className="flex flex-col sm:flex-row justify-between items-center gap-4 p-4 border rounded-md bg-muted/50 flex-wrap">
                 {/* Participant Info & Dropdown */}
                 <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                        <Button variant="outline" size="sm">
                            <Users className="mr-2 h-4 w-4" />
                            {t('page.videoCall.participants', { count: participantList.length })}
                        </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="start" className="w-64 max-h-60 overflow-y-auto">
                        <DropdownMenuLabel>{t('page.videoCall.participantListTitle')}</DropdownMenuLabel>
                        <DropdownMenuSeparator />
                        {participantList.length > 0 ? (
                            participantList.map((p) => (
                                <DropdownMenuItem key={p.id} className="flex items-center gap-2 cursor-default">
                                     {/* Use different icons for self vs others */}
                                    {p.id === socketRef.current?.id
                                        ? <UserPlus className="h-4 w-4 text-primary" />
                                        : <User className="h-4 w-4 text-muted-foreground" />
                                    }
                                    <span className="truncate font-medium">{p.name}</span>
                                    {p.id === socketRef.current?.id && <span className="text-xs text-muted-foreground ml-auto">({t('page.videoCall.you')})</span>}
                                </DropdownMenuItem>
                            ))
                        ) : (
                            <DropdownMenuItem disabled>{t('page.videoCall.noParticipants')}</DropdownMenuItem>
                        )}
                    </DropdownMenuContent>
                </DropdownMenu>

                 {/* Meeting Code & Copy */}
                 <div className="flex items-center gap-1 sm:gap-2 bg-background border rounded-md px-3 py-1.5">
                    <span className="text-xs sm:text-sm text-muted-foreground">{t('page.videoCall.meetingCodeLabel')}:</span>
                    <span className="font-mono font-semibold text-primary text-sm">{meetingCode}</span>
                    <Button variant="ghost" size="icon" onClick={copyCodeToClipboard} aria-label={t('page.videoCall.button.copyCode')} className="h-6 w-6 ml-1">
                        <Copy className="h-3.5 w-3.5" />
                    </Button>
                 </div>

                 {/* Media Controls */}
                 <div className="flex items-center gap-2">
                    <Button variant={isAudioMuted ? "destructive" : "outline"} size="icon" onClick={toggleAudio} aria-label={isAudioMuted ? t('page.videoCall.unmuteMicAria') : t('page.videoCall.muteMicAria')}>
                        {isAudioMuted ? <MicOff className="h-4 w-4" /> : <Mic className="h-4 w-4" />}
                    </Button>
                    <Button variant={isVideoOff ? "destructive" : "outline"} size="icon" onClick={toggleVideo} aria-label={isVideoOff ? t('page.videoCall.cameraOnAria') : t('page.videoCall.cameraOffAria')}>
                        {isVideoOff ? <VideoOff className="h-4 w-4" /> : <Video className="h-4 w-4" />}
                    </Button>
                 </div>

                  {/* Leave Meeting Button */}
                  <Button onClick={() => handleLeaveMeeting(true)} variant="destructive">
                    <PhoneOff className="mr-2 h-4 w-4" />
                    {t('page.videoCall.button.leaveMeeting')}
                 </Button>
              </div>
            </div>
          ) : (
            // --- Pre-Meeting View ---
            <div className="flex flex-col md:flex-row gap-6 items-stretch">
              {/* Join Meeting Card */}
              <Card className="flex-1 flex flex-col shadow-md">
                <CardHeader>
                  <CardTitle>{t('page.videoCall.joinTitle')}</CardTitle>
                  <CardDescription>{t('page.videoCall.joinDescription')}</CardDescription>
                </CardHeader>
                <CardContent className="flex-grow">
                  <div className="space-y-2">
                    <Label htmlFor="meetingCode">{t('page.videoCall.label.meetingCode')}</Label>
                    <Input
                      id="meetingCode"
                      placeholder={t('page.videoCall.placeholder.meetingCode')}
                      value={meetingCode}
                      onChange={(e) => setMeetingCode(e.target.value.toUpperCase().trim())} // Ensure uppercase and trim
                      maxLength={6}
                      className="font-mono tracking-widest text-center text-lg h-12"
                      disabled={isLoading || isConnecting || !!connectionError} // Disable if connecting or error
                      aria-required="true"
                      aria-invalid={!!connectionError} // Indicate invalid state on connection error
                    />
                  </div>
                </CardContent>
                <CardFooter className="flex justify-end">
                  <Button onClick={handleJoinMeeting} className="w-full" disabled={isLoading || isConnecting || !meetingCode || meetingCode.length !== 6 || !!connectionError}>
                    {isConnecting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <LogIn className="mr-2 h-4 w-4" />}
                    {t('page.videoCall.button.joinMeeting')}
                  </Button>
                </CardFooter>
              </Card>

              {/* Start Meeting Card (Court Official Only) */}
              {user.role === 'CourtOfficial' && (
                <Card className="flex-1 border-primary bg-primary/5 flex flex-col shadow-md">
                  <CardHeader>
                    <CardTitle>{t('page.videoCall.startTitle')}</CardTitle>
                    <CardDescription>{t('page.videoCall.startDescription')}</CardDescription>
                  </CardHeader>
                  <CardContent className="text-center flex-grow flex items-center justify-center">
                     <Button onClick={handleGenerateCode} className="w-full" disabled={isLoading || isConnecting || !!connectionError}>
                         {isConnecting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Video className="mr-2 h-4 w-4" />}
                         {t('page.videoCall.button.startMeeting')}
                    </Button>
                  </CardContent>
                   <CardFooter>
                       <p className="text-xs text-muted-foreground text-center w-full">{t('page.videoCall.startHint')}</p>
                   </CardFooter>
                </Card>
              )}
            </div>
          )}

          {/* Camera Permission Alert - Shown only before joining/starting */}
          {hasCameraPermission === false && !isMeetingActive && (
            <Alert variant="destructive" className="mt-6">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>{t('page.videoCall.error.permissionDeniedTitle')}</AlertTitle>
              <AlertDescription>
                {t('page.videoCall.error.permissionInstructions')}
                 <Button variant="link" className="p-0 h-auto ml-1 text-destructive-foreground underline" onClick={() => requestCameraPermission(() => {}, () => {})}>
                    {t('page.videoCall.button.retryPermission')}
                 </Button>
              </AlertDescription>
            </Alert>
          )}
           {/* General Instructions/Info */}
          {!isMeetingActive && !connectionError && (
             <Alert variant="default" className="mt-6">
               <Video className="h-4 w-4" />
               <AlertTitle>{t('page.videoCall.infoTitle')}</AlertTitle>
               <AlertDescription>
                   {t('page.videoCall.infoText')}
                   {user.role === 'CourtOfficial' ? ` ${t('page.videoCall.infoTextOfficial')}` : ` ${t('page.videoCall.infoTextParticipant')}`}
               </AlertDescription>
             </Alert>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
